'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { isAuthenticated, getProducts, deleteProduct, type Product } from '@/lib/storage'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import ProductForm from '@/components/ProductForm'

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([])
  const [showForm, setShowForm] = useState(false)
  const [editingProduct, setEditingProduct] = useState<Product | null>(null)
  const [deleteConfirm, setDeleteConfirm] = useState<string | null>(null)
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated()) {
      router.push('/login')
      return
    }
    loadProducts()
  }, [router])

  const loadProducts = () => {
    setProducts(getProducts())
  }

  const handleEdit = (product: Product) => {
    setEditingProduct(product)
    setShowForm(true)
  }

  const handleDelete = (id: string) => {
    if (deleteConfirm === id) {
      deleteProduct(id)
      loadProducts()
      setDeleteConfirm(null)
    } else {
      setDeleteConfirm(id)
      // Auto-cancel confirmation after 3 seconds
      setTimeout(() => setDeleteConfirm(null), 3000)
    }
  }

  const handleFormClose = () => {
    setShowForm(false)
    setEditingProduct(null)
    loadProducts()
  }

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0
    }).format(amount)
  }

  const getStockBadge = (stock: number) => {
    if (stock === 0) return <Badge variant="destructive">Habis</Badge>
    if (stock < 10) return <Badge variant="secondary">Stok Rendah</Badge>
    return <Badge variant="default">Tersedia</Badge>
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <Link href="/dashboard" className="text-muted-foreground hover:text-foreground">
                ← Kembali ke Dashboard
              </Link>
              <h1 className="text-2xl font-bold mt-2">Manajemen Produk</h1>
            </div>
            <Button onClick={() => setShowForm(true)}>
              Tambah Produk
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* Hero Image */}
        <div className="mb-8">
          <img 
            src="https://placehold.co/1200x200?text=Kelola+Produk+dan+Stok+dengan+Mudah" 
            alt="Kelola Produk dan Stok dengan Mudah" 
            onError={(e) => { e.currentTarget.style.display='none'; }} 
            className="w-full rounded-lg object-cover" 
          />
        </div>

        {/* Products Table */}
        <Card>
          <CardHeader>
            <CardTitle>Daftar Produk</CardTitle>
          </CardHeader>
          <CardContent>
            {products.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">Belum ada produk. Tambahkan produk pertama Anda!</p>
                <Button onClick={() => setShowForm(true)} className="mt-4">
                  Tambah Produk
                </Button>
              </div>
            ) : (
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Nama Produk</TableHead>
                      <TableHead>Harga</TableHead>
                      <TableHead>Stok</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Aksi</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {products.map((product) => (
                      <TableRow key={product.id}>
                        <TableCell className="font-medium">{product.nama}</TableCell>
                        <TableCell>{formatCurrency(product.harga)}</TableCell>
                        <TableCell>{product.stok}</TableCell>
                        <TableCell>{getStockBadge(product.stok)}</TableCell>
                        <TableCell>
                          <div className="flex gap-2">
                            <Button
                              variant="outline"
                              size="sm"
                              onClick={() => handleEdit(product)}
                            >
                              Edit
                            </Button>
                            <Button
                              variant={deleteConfirm === product.id ? "destructive" : "outline"}
                              size="sm"
                              onClick={() => handleDelete(product.id)}
                            >
                              {deleteConfirm === product.id ? "Konfirmasi?" : "Hapus"}
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </CardContent>
        </Card>

        {deleteConfirm && (
          <Alert className="mt-4">
            <AlertDescription>
              Klik "Konfirmasi?" sekali lagi untuk menghapus produk. Konfirmasi akan dibatalkan dalam 3 detik.
            </AlertDescription>
          </Alert>
        )}
      </main>

      {/* Product Form Modal */}
      {showForm && (
        <ProductForm
          product={editingProduct}
          onClose={handleFormClose}
        />
      )}
    </div>
  )
}
