// Utility functions for localStorage management with error handling

export interface Product {
  id: string;
  nama: string;
  harga: number;
  stok: number;
  createdAt: string;
}

export interface Transaction {
  id: string;
  productId: string;
  productName: string;
  jumlah: number;
  tipe: 'pembelian' | 'penjualan';
  status: 'diproses' | 'menunggu_persetujuan';
  total: number;
  createdAt: string;
}

export interface User {
  username: string;
  isLoggedIn: boolean;
}

// Authentication functions
export const login = (username: string, password: string): boolean => {
  try {
    // Simple hardcoded authentication
    if (username === 'admin' && password === 'admin123') {
      localStorage.setItem('isLoggedIn', 'true');
      localStorage.setItem('username', username);
      return true;
    }
    return false;
  } catch (error) {
    console.error('Error during login:', error);
    return false;
  }
};

export const logout = (): void => {
  try {
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('username');
  } catch (error) {
    console.error('Error during logout:', error);
  }
};

export const isAuthenticated = (): boolean => {
  try {
    return localStorage.getItem('isLoggedIn') === 'true';
  } catch (error) {
    console.error('Error checking authentication:', error);
    return false;
  }
};

export const getUsername = (): string => {
  try {
    return localStorage.getItem('username') || '';
  } catch (error) {
    console.error('Error getting username:', error);
    return '';
  }
};

// Product functions
export const getProducts = (): Product[] => {
  try {
    const products = localStorage.getItem('products');
    return products ? JSON.parse(products) : [];
  } catch (error) {
    console.error('Error getting products:', error);
    return [];
  }
};

export const saveProducts = (products: Product[]): void => {
  try {
    localStorage.setItem('products', JSON.stringify(products));
  } catch (error) {
    console.error('Error saving products:', error);
  }
};

export const addProduct = (product: Omit<Product, 'id' | 'createdAt'>): void => {
  try {
    const products = getProducts();
    const newProduct: Product = {
      ...product,
      id: Date.now().toString(),
      createdAt: new Date().toISOString(),
    };
    products.push(newProduct);
    saveProducts(products);
  } catch (error) {
    console.error('Error adding product:', error);
  }
};

export const updateProduct = (id: string, updatedProduct: Partial<Product>): void => {
  try {
    const products = getProducts();
    const index = products.findIndex(p => p.id === id);
    if (index !== -1) {
      products[index] = { ...products[index], ...updatedProduct };
      saveProducts(products);
    }
  } catch (error) {
    console.error('Error updating product:', error);
  }
};

export const deleteProduct = (id: string): void => {
  try {
    const products = getProducts();
    const filteredProducts = products.filter(p => p.id !== id);
    saveProducts(filteredProducts);
  } catch (error) {
    console.error('Error deleting product:', error);
  }
};

// Transaction functions
export const getTransactions = (): Transaction[] => {
  try {
    const transactions = localStorage.getItem('transactions');
    return transactions ? JSON.parse(transactions) : [];
  } catch (error) {
    console.error('Error getting transactions:', error);
    return [];
  }
};

export const saveTransactions = (transactions: Transaction[]): void => {
  try {
    localStorage.setItem('transactions', JSON.stringify(transactions));
  } catch (error) {
    console.error('Error saving transactions:', error);
  }
};

export const addTransaction = (transaction: Omit<Transaction, 'id' | 'createdAt'>): void => {
  try {
    const transactions = getTransactions();
    const newTransaction: Transaction = {
      ...transaction,
      id: Date.now().toString(),
      createdAt: new Date().toISOString(),
    };
    transactions.push(newTransaction);
    saveTransactions(transactions);

    // Update product stock
    if (transaction.tipe === 'penjualan') {
      const products = getProducts();
      const productIndex = products.findIndex(p => p.id === transaction.productId);
      if (productIndex !== -1) {
        products[productIndex].stok -= transaction.jumlah;
        saveProducts(products);
      }
    } else if (transaction.tipe === 'pembelian') {
      const products = getProducts();
      const productIndex = products.findIndex(p => p.id === transaction.productId);
      if (productIndex !== -1) {
        products[productIndex].stok += transaction.jumlah;
        saveProducts(products);
      }
    }
  } catch (error) {
    console.error('Error adding transaction:', error);
  }
};

